<template>
  <v-container>
      <v-btn to="/home">Home</v-btn>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h4>Add New Livestock</h4>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12>
        <form @submit.prevent="onAddLivestock">
          <v-layout row>
               <v-flex xs12 sm6 offset-sm3>
                 <v-text-field
                   name="livestockId"
                   label="Livestock ID"
                   id="livestockId"
                   v-model="livestockId"
                   required></v-text-field>
               </v-flex>
             </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="stockName"
                label="Stock Name"
                id="stockName"
                v-model="stockName"
                required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="breed"
                label="Breed"
                id="breed"
                v-model="breed"
                required></v-text-field>
            </v-flex>
          </v-layout> <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="numInStock"
                label="Number in Stock"
                id="numInStock"
                v-model="numInStock"
                required></v-text-field>
            </v-flex>
          </v-layout>
		   <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="date"
                label="Date Added"
                id="date"
                v-model="date"
                required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="addInfo"
                label="Additional Information"
                id="addInfo"
                multi-line
                v-model="addInfo"
                required></v-text-field>
            </v-flex>
          </v-layout>
		  <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-btn
                class="primary"
                :disabled="!formIsValid"
                type="submit">Add to Livestock</v-btn>
            </v-flex>
          </v-layout>
		  </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      livestockId: '',
      stockName: '',
      breed: '',
      numInStock: '',
      date: '',
      addInfo: '',
      creatorId: ''
    }
  },
  computed: {
    formIsValid () {
      return this.livestockId !== '' && this.stockName !== '' && this.breed !== '' &&
    this.numInStock !== '' &&
    this.date !== '' &&
    this.addInfo !== ''
    }
  },
  methods: {
    onAddLivestock () {
      if (!this.formIsValid) {
        return
      }
      const livestockData = {
        livestockId: this.livestockId,
        stockName: this.stockName,
        breed: this.breed,
        numInStock: this.numInStock,
        date: this.date,
        addInfo: this.addInfo,
        creatorId: this.creatorId
      }
      this.$store.dispatch('addLivestock', livestockData)
      this.$router.push('/livestock')
    }
  }
}
</script>



